.homeDiv {
  height: 600px;
}

.container {
  display: flex;
}

.start {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.leftContainer {
  height: 330px;
  width: 300px;
  margin-top: 130px;
  margin-left: 250px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(252, 249, 249, 0.1) 10px 20px 50px;
}

.rightContainer {
  font-size: 24px;
  margin: 100px 0px 0px 200px;
  color: white;
  font-family: "Roboto Slab", serif;
  line-height: 50px;
}

.resumeBtn {
  padding: 0.8em 1.8em;
  border: 3px solid #545fc4;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  /* text-transform: uppercase; */
  font-size: 16px;
  font-weight: bold;
  transition: 0.3s;
  z-index: 1;
  font-family: inherit;
  color: #545fc4;
  margin-top: 20px;
  cursor: pointer;
}

.resumeBtn::before {
  content: "";
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #545fc4;
  transition: 0.5s ease;
  display: block;
  z-index: -1;
}

.resumeBtn:hover {
  color: white;
}

.resumeBtn:hover::before {
  width: 105%;
}
