.aboutDiv {
  height: 600px;
  display: grid;
  grid-template-columns: repeat(2, 50% 50%);
  gap: 20px;
}

.description {
  margin-left: 150px;
  margin-top: 80px;
  text-align: center;
}

.logo {
  width: 500px;
  height: 500px;
  border-radius: 15px;
  margin: 70px 0px 0px 60px;
}

.heading {
  color: #545fc4;
  font-family: "Roboto Slab", serif;
  text-decoration: underline;
  font-size: 40px;
}

