.card {
  width: 350px;
  height: 450px;
  background-image: linear-gradient(163deg, #4c04b8 0%, #42b3f4 100%);
  border-radius: 20px;
  transition: all 0.3s;
}

.card2 {
  width: 350px;
  height: 450px;
  background-color: #131010;
  border-radius: 20px;
  transition: all 0.2s;
}

.card2:hover {
  transform: scale(0.98);
  border-radius: 20px;
}

.card:hover {
  box-shadow: 0px 0px 30px 1px rgba(39, 162, 203, 0.3);
}

.card-image {
  width: 85%;
  margin: 28px 0px 0px 28px;
  border-radius: 10px;
  height: 170px;
}

.para {
  color: white;
  text-align: center;
  margin: 0px 20px;
}

.ts-img {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}

.uiverse {
  padding: 0.8em 1.8em;
  border: 1px solid #545fc4;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  /* text-transform: uppercase; */
  font-size: 16px;
  transition: 0.3s;
  z-index: 1;
  font-family: inherit;
  color: #545fc4;
  margin: 25px 0px 0px 46px;
  cursor: pointer;
}

.uiverse::before {
  content: "";
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #545fc4;
  transition: 0.5s ease;
  display: block;
  z-index: -1;
}

.uiverse:hover {
  color: white;
}

.uiverse:hover::before {
  width: 105%;
}
